import {InputUserPanel, OInputUserInit} from "back/core/widgets/inputs";
import {EUserAspects} from "lib/core/user";
import {OUserRefInit, UserRef} from "back/core/widgets/userRef";
import {IRoleEditUiHandlerContext, RoleEditUiHandler, ROLES} from "lib/commons/roles";
